.title {
  margin-bottom: 25px;
}

.dialogs {
  display: grid;
  grid-template-columns: 3fr 9fr;
}

.dialogItems {
}

.dialogItems .active {
  color: rgb(194, 0, 107);
}

.dialog:not(:last-child) {
  margin-bottom: 20px;
}

.messages {
}

.message:not(:last-child) {
  margin-bottom: 20px;
}

.nameWrap {
  display: flex;
  align-items: center;
}

.img {
  width: 70px;
  border-radius: 50%;
  margin-right: 15px;
}

.name {
  display: block;
}
